import { Component } from '@angular/core';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { NextObserver } from 'rxjs';
import { ProjectsService } from './services/projects.service';
import { ProjectDataViewModel } from './view-models/project-data-viewmodel';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  id: number | undefined;


  title = 'Subhadeep Jasu';
  externalComponentOpen = false;
  routerComponentOpen = false;
  mode = -1;
  currentURL = '';

  constructor(private route: ActivatedRoute, private router: Router, private projectService: ProjectsService) {
    router.events.subscribe((_: any) => {
      if (_.url !== undefined) {
        this.currentURL = _.url;
      }
      if (this.currentURL !== '/') {
        this.routerComponentOpen = true;
        this.externalComponentOpen = false;
      } else {
        this.routerComponentOpen = false;
      }
    });
    this.route.fragment.subscribe((params) => {
      this.getProject (params);
      console.log(params);
    });
  }

  getProject(repo?: string): void {
    this.projectService.getProjects ().subscribe ((projects: ProjectDataViewModel[]) => {
      let i = 0;
      while (true) {
        if (projects[i] === undefined){
          break;
        }
        if (projects[i].repository === repo){
          this.id = i;
          break;
        }
        i++;
      }
      if (this.id !== undefined) {
        this.redirect();
      }
    });
  }
  redirect(): void {
    this.router.navigate(['project/' + this.id?.toString()]);
  }

  openRouterView(mode: number): any {
    this.mode = mode;
    this.externalComponentOpen = true;
  }
  closeRouterView(): any {
    this.externalComponentOpen = false;
  }

}
